.q-field--outlined.q-field--rounded .q-field__control {
    background-color: rgb(0 0 0 / 5%);
    border-radius: 6px;
}

/* .q-field--outlined.q-field--rounded .q-field__control {
    border-radius: 28px;
} */
.q-field--dense .q-field__control,
.q-field--dense .q-field__marginal {
    height: 40px;
}

.q-field--outlined .q-field__control {
    border-radius: 4px;
    padding: 0 12px;
}

.relative-position {
    position: relative;
}

input[type=number] {
    appearance: textfield;
}


.q-field__native,
.q-field__input {
    width: 100%;
    min-width: 0;
    outline: 0 !important;
    user-select: auto;
}

.q-field__native,
.q-field__prefix,
.q-field__suffix,
.q-field__input {
    font-weight: 400;
    line-height: 28px;
    letter-spacing: .00937em;
    text-decoration: inherit;
    text-transform: inherit;
    border: none;
    border-radius: 0;
    background: none;
    /* color: #000000de; */
    outline: 0;
    padding: 6px 0;
}

.q-field__after {
    padding-left: 0 !important;
}

.q-field--dense .q-field__after,
.q-field--dense .q-field__append {
    padding-left: 6px;
}

.q-field--dense .q-field__control,
.q-field--dense .q-field__marginal {
    height: 40px;
}

.q-field--dark:not(.q-field--highlighted) .q-field__label,
.q-field--dark .q-field__marginal,
.q-field--dark .q-field__bottom {
    color: #ffffffb3;
}

.no-wrap {
    flex-wrap: nowrap;
}

