    }

    .flex>.col-xs-shrink {
        width: auto;
        min-width: 0;
        max-width: 100%;
    }

    .col,
    .col-xs {
        flex: 10000 1 0%;
    }

    .row>.col-9,
    .row>.col-xs-9 {
